import { Route, Switch  } from 'react-router-dom';
import Header from "./components/Header/Header";
import Home from "./containers/Home/Home";
import Details from "./containers/Details/Details";

function App() {

  return (
    <div className="App">
      <Header/>
      <Switch>
        <Route path="/projects/learning/rest-countries-api-with-color-theme-switcher" component={Home} exact/>
        <Route path="/projects/learning/rest-countries-api-with-color-theme-switcher/home" component={Home} exact/>
        <Route path="/projects/learning/rest-countries-api-with-color-theme-switcher/countries" component={Home} exact/>
        <Route path="/projects/learning/rest-countries-api-with-color-theme-switcher/details/:country" component={Details}/>
      </Switch>
    </div>
  );
}

export default App;
